import React from "react"
import Dialog from '@material-ui/core/Dialog';
import { BiLinkExternal } from "react-icons/bi"
import { FaTimes } from "react-icons/fa"
import { AccountContainer, DialogTitle, AccountLink, DiaglogText, AccountIcon, CloseIcon } from "./DialogElement"
import { Button } from "../ButtonElements";
import { getChainExplorerUrl, getChainExplorerName } from "../../utils/getChainExplorer";
import { Grid } from "@material-ui/core"
import styled from "styled-components";

const StyledButton = styled(Button)`
    width:200px;
    margin-top:20px;
`

export default function AccountDialog({ open, handleClose, account, disconnectNetwork }) {
    return (
        <div>
            <Dialog
                open={open}
                onClose={(event, reason) => {
                    if (reason !== 'backdropClick') {
                        handleClose();
                    }
                }}
            >
                <AccountContainer>
                    <CloseIcon onClick={handleClose}>
                        <FaTimes />
                    </CloseIcon>

                    <DialogTitle style={{ textAlign: "left" }}>Your Wallet</DialogTitle>

                    <DiaglogText>{account}</DiaglogText>

                    <Grid container spacing={6}>
                        <Grid container item sm={12} md={6} alignItems="center">
                            <AccountLink href={getChainExplorerUrl(account)} target="_blank">
                                {`View on ${getChainExplorerName()}`}

                                <AccountIcon>
                                    <BiLinkExternal />
                                </AccountIcon>
                            </AccountLink>
                        </Grid>
                    </Grid>

                    <StyledButton onClick={disconnectNetwork}>
                        Disconnect
                    </StyledButton>
                </AccountContainer>
            </Dialog>
        </div >
    );
}