import { useState, useEffect } from "react";
import { useDuckContract } from "../useContract";
import useRefresh from "../useRefresh";

const AIRDROP_ROLE = process.env.REACT_APP_AIRDROP_ROLE

export const useGetBaseInfo = () => {
    const [baseInfo, setBaseInfo] = useState({})
    const contract = useDuckContract()

    useEffect(() => {
        let mounted = true;
        const fetchData = async () => {
            try {
                const [flatPrice, maxAdoption] = await Promise.all([
                    contract.methods.FLAT_PRICE().call(),
                    contract.methods.MAX_ADOPTION().call(),
                ])

                if (mounted) {
                    setBaseInfo({
                        flatPrice,
                        maxAdoption
                    })
                }
            } catch (err) {
                console.log("Unable to fetch base info", err.response)
            }
        }
        fetchData();
        return () => { mounted = false }
    }, [])// eslint-disable-line react-hooks/exhaustive-deps
    return baseInfo
}

export const useGetInfoUsingAccount = (account) => {
    const [info, setInfo] = useState({})
    const contract = useDuckContract()
    const { fastRefresh } = useRefresh()

    useEffect(() => {
        let mounted = true;
        const fetchData = async () => {
            try {
                if (account) {
                    const [hasRole, airdropSuccess] = await Promise.all([
                        contract.methods.hasRole(AIRDROP_ROLE, account).call(),
                        contract.methods.airdropSuccess(account).call()
                    ])

                    if (mounted) {
                        setInfo({
                            hasRole,
                            airdropSuccess,
                        })
                    }
                }

            } catch (err) {
                console.log("Unable to fetch airdrop role info", err.response)
            }
        }
        fetchData();
        return () => { mounted = false }
    }, [account, contract.methods, fastRefresh])
    return info
}