import React, { useState } from "react"
import { TextInfo, BlockchainButton, BlockchainContainer, NumberButton, NumberContainer, NumberInput } from "./MintCardElements"
import { useGetBaseInfo } from "../../hooks/duck";
import { useWallet } from 'use-wallet'
import { useDuckContract } from "../../hooks/useContract";
import TransactionDialog from "../Dialog/TranscationDialog";
import BigNumber from "bignumber.js";
import { getBalanceNumber } from "../../utils/formatBalance";

const Sales = () => {
    const [inputValue, setInputValue] = useState(1)
    const [transactionHash, setTransactionHash] = useState("")
    const [loading, setLoading] = useState(false)
    const [error, setError] = useState("")
    const baseInfo = useGetBaseInfo()
    const maxValue = Number(baseInfo.maxAdoption) || 50
    const minPrice = new BigNumber(baseInfo.flatPrice).multipliedBy(inputValue) || new BigNumber(50000000000000000).multipliedBy(inputValue)
    const { account } = useWallet()
    const duckContract = useDuckContract()
    const [open, setOpen] = React.useState(false);
    const isAddressNull = account === null

    const minusValue = () => {
        setInputValue(x => x - 1)
    }

    const addValue = () => {
        setInputValue(x => x + 1)
    }

    const handleInputValue = (e) => {
        if (Number(e.target.value) > 50) {
            setInputValue(50)
        } else if (Number(e.target.value) < 1) {
            setInputValue(1)
        } else {
            setInputValue(Number(e.target.value))
        }
    }

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClickClose = () => {
        setOpen(false);
    };

    const adoptDucks = async () => {
        setError("")
        setLoading(true)
        setTransactionHash("")
        handleClickOpen()
        try {
            const result = await duckContract.methods.adoptDucks(inputValue).send({ from: account, value: minPrice })
            if (result) {
                setTransactionHash(result.transactionHash)
                setLoading(false)
            }
        } catch (err) {
            setError("Oops! Failed to mint the dumb duck! Please try again")
            setLoading(false)
        }

    }

    return (
        <BlockchainContainer>
            <TextInfo>Total Cost: {getBalanceNumber(minPrice)} ETH</TextInfo>
            <BlockchainButton onClick={adoptDucks} disabled={isAddressNull}>
                Mint
            </BlockchainButton>
            <NumberContainer>
                <NumberButton onClick={minusValue} disabled={inputValue <= 1}>-</NumberButton>
                <NumberInput
                    type="number"
                    inputmode="numeric"
                    max={maxValue}
                    min={1}
                    value={inputValue}
                    onChange={handleInputValue} />
                <NumberButton onClick={addValue} disabled={inputValue >= maxValue}>+</NumberButton>
            </NumberContainer>

            <TextInfo>{`You can mint up to ${maxValue} ducks each time`}</TextInfo>

            <TransactionDialog open={open} handleClose={handleClickClose} transactionHash={transactionHash} loading={loading} error={error} />
        </BlockchainContainer>
    )
}

export default Sales