import React, { useState, useEffect } from 'react';
import { FaBars } from 'react-icons/fa';
import { IconContext } from 'react-icons/lib';
import { animateScroll as scroll } from 'react-scroll';
import {
    Nav,
    NavbarContainer,
    NavLogo,
    MobileIcon,
    NavMenu,
    NavItem,
    NavLinks,
    ConnectionButton,
    ConnectionText
} from './NavbarElements';
import { useWallet } from 'use-wallet'
import Web3 from "web3"
import truncateWalletAddress from '../../utils/truncateWalletAddress';
import { hexChainId } from '../../config';
import { chainInfo } from "../../config/index"
import AccountDialog from '../Dialog/AccountDialog';

const chainId = Number(process.env.REACT_APP_CHAIN_ID)
const hexId = hexChainId[chainId]

const Navbar = ({ toggle }) => {
    const initialState = window.innerWidth >= 1000 ? true : false
    const [scrollNav, setScrollNav] = useState(false)
    const [windowWidth, setWindowWidth] = useState(initialState)
    const wallet = useWallet()
    const account = wallet.account
    const [open, setOpen] = useState(false);

    const changeNav = () => {
        if (window.scrollY >= 80) {
            setScrollNav(true)
        } else {
            setScrollNav(false)
        }
    }

    const updateWidth = () => {
        if (window.innerWidth >= 1200) {
            setWindowWidth(true)
        } else {
            setWindowWidth(false)
        }
    }

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClickClose = () => {
        setOpen(false);
    };

    useEffect(() => {
        window.addEventListener('scroll', changeNav)
        window.addEventListener('resize', updateWidth)

        return () => {
            window.removeEventListener('scroll', changeNav)
            window.removeEventListener('resize', updateWidth)
        }
    }, [])

    const toggleHome = () => {
        scroll.scrollToTop();
    }

    const connectNetwork = async () => {
        if (window.ethereum) {
            window.web3 = new Web3(window.ethereum);
            await window.ethereum.enable();

            try {
                if (chainId === 1 || chainId === 3) {
                    await window.ethereum.request({
                        method: 'wallet_switchEthereumChain',
                        params: [{ chainId: hexId }],
                    });
                    wallet.connect();
                } else {
                    await window.ethereum.request({
                        method: 'wallet_addEthereumChain',
                        params: [chainInfo[chainId]]
                    })

                    await window.ethereum.request({
                        method: 'wallet_switchEthereumChain',
                        params: [{ chainId: hexId }],
                    });

                    wallet.connect();
                }
            } catch (error) {
                console.log(error);
            }
        } else {
            window.alert('Non-Ethereum Browser detected. Please install metamask first!');
        }
    }

    const disconnectNetwork = () => {
        handleClickClose()
        wallet.reset()
    }

    const openDialog = () => {
        handleClickOpen()
    }

    return (
        <>
            <IconContext.Provider value={{ color: '#fff' }}>
                <Nav>
                    <NavbarContainer scrollNav={scrollNav}>
                        <NavLogo to='/' onClick={toggleHome}>
                            {
                                windowWidth ? <img src="./duckLogo.svg" alt="logo" style={{ width: "250px" }} /> : <img src="./logoOnly.svg" alt="logo" style={{ width: "50px" }} />
                            }

                        </NavLogo>

                        <MobileIcon onClick={toggle}>
                            <FaBars />
                        </MobileIcon>
                        <NavMenu>
                            <NavItem>
                                <NavLinks
                                    to='home'
                                    smooth={true}
                                    duration={500}
                                    spy={true}
                                    exact='true'
                                >Home</NavLinks>
                            </NavItem>

                            <NavItem>
                                <NavLinks
                                    to='collection'
                                    smooth={true}
                                    duration={500}
                                    spy={true}
                                    exact='true'
                                >Collections</NavLinks>
                            </NavItem>

                            <NavItem>
                                <NavLinks
                                    to='roadmap'
                                    smooth={true}
                                    duration={500}
                                    spy={true}
                                    exact='true'
                                >Roadmap</NavLinks>
                            </NavItem>

                            <NavItem>
                                <NavLinks
                                    to='team'
                                    smooth={true}
                                    duration={500}
                                    spy={true}
                                    exact='true'
                                >Team</NavLinks>
                            </NavItem>
                        </NavMenu>

                        {
                            account ? <ConnectionButton>
                                <ConnectionText onClick={openDialog}>{truncateWalletAddress(account)}</ConnectionText>
                            </ConnectionButton> :
                                <ConnectionButton>
                                    <ConnectionText onClick={connectNetwork}>Connect</ConnectionText>
                                </ConnectionButton>
                        }
                    </NavbarContainer>
                </Nav>
            </IconContext.Provider>

            <AccountDialog open={open} handleClose={handleClickClose} account={account} disconnectNetwork={disconnectNetwork} />
        </>
    )
}

export default Navbar
