import { useEffect, useState } from 'react'
import dumbduck from "../config/abi/dumbduck.json"
import { getDuckContract } from '../utils/addressHelpers'
import useWeb3 from './useWeb3'

const useContract = (abi, address) => {
    const web3 = useWeb3()
    const [contract, setContract] = useState(new web3.eth.Contract(abi, address))

    useEffect(() => {
        setContract(new web3.eth.Contract(abi, address))
    }, [abi, address, web3])

    return contract
}

export const useDuckContract = () => {
    return useContract(dumbduck, getDuckContract())
}