import React from "react"
import Dialog from '@material-ui/core/Dialog';
import { FiArrowUpCircle } from "react-icons/fi"
import { MdErrorOutline } from "react-icons/md"
import { LayoutContainer, SubmissionIcon, DialogTitle, TransactionLink, ErrorIcon, ErrorText } from "./DialogElement"
import { Button } from "../ButtonElements";
import { getTranscationHash, getChainExplorerName } from "../../utils/getChainExplorer";
import Slide from '@material-ui/core/Slide';
import styled from "styled-components";
import LoadingContent from "../Loading";

const StyledButton = styled(Button)`
    width:200px;
`

const StyledContainer = styled(LayoutContainer)`
    padding:10px;
`

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

export default function TransactionDialog({ open, handleClose, transactionHash, loading, error }) {
    let title;
    let comp;

    if (loading) {
        title = "Waiting for Confirmation"
        comp = <LoadingContent />
    } else if (error) {
        title = "Error Message"
        comp = <StyledContainer>
            <ErrorIcon>
                <MdErrorOutline style={{ width: "80px", height: "80px" }} />
            </ErrorIcon>

            <ErrorText>
                {error}
            </ErrorText>

            <StyledButton onClick={handleClose} color="primary">
                CLOSE
            </StyledButton>
        </StyledContainer>
    } else {
        title = "Transaction Submitted"
        comp = <StyledContainer>
            <SubmissionIcon>
                <FiArrowUpCircle style={{ width: "80px", height: "80px" }} />
            </SubmissionIcon>

            <TransactionLink href={getTranscationHash(transactionHash)} target="_blank">
                {`View on ${getChainExplorerName()}`}
            </TransactionLink>

            <StyledButton onClick={handleClose} color="primary">
                CLOSE
            </StyledButton>
        </StyledContainer>
    }


    return (
        <div>
            <Dialog
                open={open}
                onClose={(event, reason) => {
                    if (reason !== 'backdropClick') {
                        handleClose();
                    }
                }}

                TransitionComponent={Transition}
            >
                <LayoutContainer>
                    <DialogTitle error={error}>{title}</DialogTitle>
                    {comp}
                </LayoutContainer>
            </Dialog>
        </div >
    );
}