import React, { useState } from "react"
import { CardOuterBox, CardInnerBox, InnerImg, CountdownBox, TextInfo, BlockchainButton, ErrorTextInfo, CustomSpan } from "./MintCardElements"
import Countdown from 'react-countdown';
import Sale from "./sales";
import { useWallet } from 'use-wallet'
import { useDuckContract } from "../../hooks/useContract";
import TransactionDialog from "../Dialog/TranscationDialog";

const MintCard = ({ infoWithAccount }) => {
    const [startDate] = useState(1631012400000)
    const [transactionHash, setTransactionHash] = useState("")
    const [loading, setLoading] = useState(false)
    const [open, setOpen] = React.useState(false);
    const [error, setError] = useState("")
    const hasRole = infoWithAccount.hasRole
    const airdropSuccess = infoWithAccount.airdropSuccess

    const { account } = useWallet()
    const isAddressNull = account === null

    const duckContract = useDuckContract()

    const claimNft = async () => {
        setError("")
        setLoading(true)
        setTransactionHash("")
        handleClickOpen()
        try {
            const result = await duckContract.methods.claim().send({ from: account })

            if (result) {
                setTransactionHash(result.transactionHash)
                setLoading(false)
            }
        } catch (err) {
            setError("Oops! Failed to claim the dumb duck! Please try again")
            setLoading(false)
        }
    }

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClickClose = () => {
        setOpen(false);
    };

    let comp;

    if (!isAddressNull) {
        if (hasRole) {
            if (airdropSuccess) {
                comp = <ErrorTextInfo>You have already claimed the Dumb Duck!</ErrorTextInfo>
            } else {
                comp = <TextInfo>Congrats, you have 1 free Dumb Duck claim!</TextInfo>
            }
        } else {
            comp = <ErrorTextInfo>Unfortunately, your address is not whitelisted!</ErrorTextInfo>
        }
    }

    return (
        <div style={{ display: "flex", flexDirection: "column", alignItems: "center" }}>
            <CardOuterBox>
                <CardInnerBox>
                    <InnerImg src="./images/motion.gif" />
                </CardInnerBox>
            </CardOuterBox>

            <Countdown date={startDate} renderer={renderer} />

            <BlockchainButton disabled={isAddressNull || !hasRole || airdropSuccess} onClick={claimNft}>
                Free Claim
            </BlockchainButton>

            <TransactionDialog open={open} handleClose={handleClickClose} transactionHash={transactionHash} loading={loading} error={error} />

            {comp}
        </div >
    )
}

const renderer = ({ total, days, hours, minutes, seconds }) => {
    if (total) {
        return (
            <div style={{ display: "flex", flexDirection: "column", alignItems: "center" }}>
                <CountdownBox>
                    <CustomSpan>{days}d:{hours}h:{minutes}m:{seconds}s</CustomSpan>
                </CountdownBox>

                <TextInfo>The duck sale is starting soon.</TextInfo>
            </div>

        );
    } else {
        return <Sale />;
    }
};

export default MintCard