import { chainExplorer, openSeaUrl } from '../config'

const chainId = process.env.REACT_APP_CHAIN_ID

export const getTranscationHash = (hash) => {
    return `${chainExplorer[chainId].url}/tx/${hash}`
}

export const getChainExplorerUrl = (address) => {
    return `${chainExplorer[chainId].url}/address/${address}`
}

export const getChainExplorerName = () => {
    return chainExplorer[chainId].name
}

export const getOpenSeaUrl = () => {
    return openSeaUrl[chainId]
}