import React from "react"
import { SocialContainer, SocialIcon, FooterContainer, TextWrapper, SocialBox, ContractLink, ContractBox, WebsiteRights } from "./FooterElements"
import { FaTwitter } from 'react-icons/fa';
import { getChainExplorerUrl } from "../../utils/getChainExplorer";
import { getDuckContract } from '../../utils/addressHelpers'
import { getOpenSeaUrl } from "../../utils/getChainExplorer"
import { animateScroll as scroll } from 'react-scroll';


const Footer = () => {
    const toggleHome = () => {
        scroll.scrollToTop();
    }
    return (
        <FooterContainer>
            <TextWrapper>
                <SocialBox>
                    <SocialContainer href="https://twitter.com/DumbDuckCamp" target="_blank">
                        <SocialIcon>
                            <FaTwitter color="#fff" />
                        </SocialIcon>
                    </SocialContainer>

                    <SocialContainer href={getOpenSeaUrl()} target="_blank">
                        <SocialIcon>
                            <img src="./images/opensea.svg" alt="open sea" style={{ width: "30px" }} />
                        </SocialIcon>
                    </SocialContainer>

                    <SocialContainer href="https://discord.gg/W99auUVXuP" target="_blank">
                        <SocialIcon>
                            <img src="./images/discord.svg" alt="open sea" style={{ width: "30px" }} />
                        </SocialIcon>
                    </SocialContainer>
                </SocialBox>

                <ContractBox>
                    <ContractLink href={`${getChainExplorerUrl(getDuckContract())}`} target="_blank">Smart Contract</ContractLink>
                </ContractBox>
            </TextWrapper>

            <WebsiteRights onClick={toggleHome}>Dumb Duck Campsite  {new Date().getFullYear()} All rights reserved</WebsiteRights>

        </FooterContainer>
    )

}

export default Footer